let host = 'http://localhost:3000/';

async function cambiarPassword(id) {
    try {
        const url = host + 'users/password';

        const datos = new FormData();
        const password = document.querySelector('#password');

        datos.append('id', id);
        datos.append('password', password.value);
        const opciones = {
            method: 'POST',
            body: datos
        }

        const resultado = await fetch(url, opciones);
        const respuesta = await resultado.json();

        alert(respuesta.message);
        window.location.href = "/users";

    } catch (error) {
        console.log(error);
    }
}

async function eliminarRegistro(id, ruta, login = false) {
    try {
        const url = host + ruta + '/delete';

        const datos = new FormData();

        datos.append('id', id);

        const opciones = {
            method: 'POST',
            body: datos
        }

        const resultado = await fetch(url, opciones);
        const respuesta = await resultado.json();

        alert(respuesta.message);
        if(login) {
            window.location.href = "/";
        } else {
            window.location.href = "/" + ruta;
        }

    } catch (error) {
        console.log(error);
    }
}

async function cambiarNombre(id) {
    try {
        const url = host + 'categories/update';

        const datos = new FormData();
        const name = document.querySelector('#categoryName' + id);

        datos.append('id', id);
        datos.append('name', name.value);
        const opciones = {
            method: 'POST',
            body: datos
        }

        const resultado = await fetch(url, opciones);
        const respuesta = await resultado.json();

        alert(respuesta.message);
        window.location.href = "/categories";

    } catch (error) {
        console.log(error);
    }
}

const product_ID = document.querySelector("#product_ID");
if (product_ID) {
    const url = window.location.search;
    const params = new URLSearchParams(url);

    product_ID.addEventListener('input', function (e) {
        const productoSeleccionado = e.target.value;
        params.set("product", productoSeleccionado);
        window.history.replaceState({}, '', `${window.location.pathname}?${params}`);
        location.reload()
    });

    if (params.has("product")) {
        const quantity = document.querySelector('#quantity');
        quantity.disabled = false;

        quantity.addEventListener('input', function (e) {
            const cantidadSeleccionada = e.target.value;
            params.set("quantity", cantidadSeleccionada);
            window.history.replaceState({}, '', `${window.location.pathname}?${params}`);
            location.reload()
        });
    }
}